import React from 'react';

import styles from "./IndBlogDescription.module.scss"
import IndBlogUserBlock from "../IndBlogUserBlock/IndBlogUserBlock";

function IndBlogDescription({content}) {
    return (
        <section className={styles['indBlogDescription']}>
            <div className="container">
                <div className={styles['indBlogDescription__userBlock']}>
                    <IndBlogUserBlock/>
                </div>
                <div
                    className={`${styles["indBlogDescription__text"]} contentTextDef`}
                    dangerouslySetInnerHTML={{ __html: content}}
                />
            </div>
        </section>
    );
}

export default IndBlogDescription;